<template>
  <div class="member-order">
    <!-- tab组件 -->
    <XtxTabs v-model="activeName" @tab-click="tabClick">
      <XtxTabsPanel
        v-for="item in orderStatus"
        :key="item.name"
        :label="item.label"
        :name="item.name"
      >
      </XtxTabsPanel>
    </XtxTabs>
    <!-- 订单列表 item单条订单-->
    <div class="order-list">
      <div v-if="loading" class="loading"></div>
      <div class="none" v-if="!loading && orderList.length === 0">暂无数据</div>
      <OrderItem
        @on-delete="handlerDelete"
        @on-cancel="handlerCancel"
        @on-confirm="handlerConfirm"
        @on-logistics="handlerLogistics"
        v-for="item in orderList"
        :key="item.id"
        :order="item"
      />
    </div>
    <!-- 分页组件 -->
    <XtxPagination
      v-if="total > 0"
      :currentPage="reqParams.page"
      :pageSize="reqParams.pageSize"
      :total="total"
    />
    <!-- 取消原因组件 -->
    <OrderCancel ref="orderCancelCom" />
    <!-- 查看物流组件 -->
    <OrderLogistics ref="orderLogisticsCom" />
  </div>
</template>

<script>
import OrderLogistics from './components/order-logistics.vue'
import OrderCancel from './components/order-cancel.vue'
import { orderStatus } from '@/api/constants'
import OrderItem from './components/order-item.vue'
import { findOrderList, delteOrder, confirmOrder } from '@/api/order'
import { reactive, ref, watch } from 'vue'
import Confirm from '@/components/library/Confirm'
import Message from '@/components/library/Message'
export default {
  name: 'MemberOrder',
  components: { OrderItem, OrderCancel, OrderLogistics },
  setup () {
    // 数据总条数
    const total = ref(0)
    // 加载状态
    const loading = ref(false)
    // 订单列表
    const orderList = ref([])
    const activeName = ref('all')
    // 筛选条件
    const reqParams = reactive({
      page: 1,
      pageSize: 10,
      orderState: 0
    })
    // 点击选项卡
    const tabClick = ({ index }) => {
      reqParams.page = 1
      reqParams.orderState = index
    }
    const getOrderList = () => {
      // 加载状态
      loading.value = true
      findOrderList(reqParams).then(data => {
        // console.log('我的订单：', data.result)
        orderList.value = data.result.items
        total.value = data.result.counts
        loading.value = false
      })
    }
    // 监听reqParams,如果发生变化，就重新发起请求
    watch(reqParams, () => {
      getOrderList()
    }, { immediate: true })
    // 删除订单
    const handlerDelete = (order) => {
      Confirm({ text: '亲，您确认删除订单吗？' }).then(() => {
        delteOrder([order.id]).then(data => {
          Message({ type: 'success', text: '删除成功' })
          getOrderList()
        })
      }).catch(() => { })
    }

    return {
      activeName,

      tabClick,
      orderStatus,
      orderList,
      loading,
      total,
      handlerDelete,
      reqParams,
      ...useCancel(),
      ...useConfirm(),
      ...useLogistics()
    }
  }
}
// 查看物流
export const useLogistics = () => {
  const orderLogisticsCom = ref(null)
  const handlerLogistics = (order) => {
    orderLogisticsCom.value.open(order)
  }
  return { handlerLogistics, orderLogisticsCom }
}
// 确认收货
export const useConfirm = () => {
  const handlerConfirm = (order) => {
    Confirm({ text: '亲，您确认收货吗？确认后货款将打给卖家。' }).then(() => {
      confirmOrder(order.id).then(data => {
        Message({ type: 'success', text: '确认收货成功' })
        // 待收货--待评价
        order.orderState = 4
      })
    }).catch(() => { })
  }
  return { handlerConfirm }
}
// 取消订单
export const useCancel = () => {
  const orderCancelCom = ref(null)
  const handlerCancel = (order) => {
    orderCancelCom.value.open(order)
  }
  return { handlerCancel, orderCancelCom }
}
</script>

<style lang="less" scoped>
.member-order {
  height: 100%;
  background-color: #fff;
}
.order-list {
  background: #fff;
  padding: 20px;
  min-height: 400px;
  position: relative;
}
.loading {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.9) url(../../../assets/images/loading.gif)
    no-repeat center;
}
.none {
  height: 400px;
  text-align: center;
  line-height: 400px;
  color: #999;
}
</style>
